@font-face {
  font-family: kxiconfont;
  src: url(iconfont.eot?t=1534989522363);
  src: url(iconfont.eot?t=1534989522363#iefix) format("embedded-opentype"), url(data:application/x-font-woff;charset=utf-8;base64,) format("woff"), url(iconfont.ttf?t=1534989522363) format("truetype"), url(iconfont.svg?t=1534989522363#iconfont) format("svg")
}

.kxiconfont {
  font-family: kxiconfont !important;
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale
}

.icon-left:before {
  content: "\e619"
}
.icon-last:before {
  content: "\e61e"
}

.icon-first:before {
  content: "\e620"
}

.icon-right:before {
  content: "\e64d"
}

.icon-clock:before {
  content: "\e61f"
}


.c-datepicker-picker {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  width: 296px;
  position: absolute;
  z-index: 2001;
  color: #333;
  //border: 1px solid #e4e7ed;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, .2);
  background: #fff;
  border-radius: 6px;
  line-height: 30px;
  margin: 5px 0;
  display: none;
  a {
    color: #333;
    &:hover{
      text-decoration:none;
    }
  }
  *:before,
  *:after {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box
  }
}

.c-datepicker-date-picker.has-time .c-datepicker-picker__body-wrapper {
  position: relative
}

.c-datepicker-date-picker__time-header {
  position: relative;
  border-bottom: 1px solid #e4e4e4;
  font-size: 12px;
  padding: 8px 5px 5px;
  display: table;
  width: 100%;
  box-sizing: border-box
}

.c-datepicker-date-picker__editor-wrap {
  position: relative;
  display: table-cell;
  padding: 0 5px
}

.c-datepicker-input {
  position: relative;
  font-size: 14px;
  display: inline-block;
  width: 100%
}

.c-datepicker-input--small {
  font-size: 13px
}

.c-datepicker-input__inner {
  -webkit-appearance: none;
  background-color: #fff;
  background-image: none;
  border-radius: 4px;
  border: 1px solid #dcdfe6;
  box-sizing: border-box;
  color: #333;
  display: inline-block;
  font-size: inherit;
  height: 40px;
  line-height: 40px;
  outline: 0;
  width: 100%;
  padding: 0 10px
}

.c-datepicker-input--small .c-datepicker-input__inner {
  height: 32px;
  line-height: 32px
}

.c-datepicker-time-panel {
  margin: 5px 0;
  border: 1px solid #e4e7ed;
  background-color: #fff;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, .1);
  border-radius: 2px;
  position: absolute;
  width: 210px;
  left: -40px;
  z-index: 1000;
  user-select: none
}

.c-datepicker-time-panel__content {
  font-size: 0;
  position: relative;
  overflow: hidden
}

.c-datepicker-time-panel__content:before,
.c-datepicker-time-panel__content:after {
  content: "";
  top: 50%;
  position: absolute;
  margin-top: -15px;
  height: 32px;
  z-index: -1;
  left: 0;
  right: 0;
  box-sizing: border-box;
  padding-top: 6px;
  text-align: left;
  border-top: 1px solid #e4e7ed;
  border-bottom: 1px solid #e4e7ed
}

.c-datepicker-time-panel__content:after {
  left: 50%;
  margin-left: 12%;
  margin-right: 12%
}

.c-datepicker-time-panel__content.has-seconds:after {
  left: 66.66667%
}

.c-datepicker-time-spinner {
  width: 100%;
  white-space: nowrap
}

.c-datepicker-time-spinner.has-seconds .c-datepicker-time-spinner__wrapper {
  width: 33.3%
}

.c-datepicker-time-spinner__wrapper {
  height: 190px;
  overflow: auto;
  display: inline-block;
  width: 50%;
  vertical-align: top;
  position: relative
}

.c-datepicker-scrollbar {
  overflow: hidden;
  position: relative
}

.c-datepicker-scrollbar::-webkit-scrollbar {
  display: none
}

.c-datepicker-scrollbar__wrap {
  overflow: scroll;
  height: 100%
}

.c-datepicker-time-spinner__wrapper .c-datepicker-scrollbar__wrap:not(.c-datepicker-scrollbar__wrap--hidden-default) {
  padding-bottom: 15px
}

.c-datepicker-time-spinner__list {
  margin: 0;
  list-style: none
}

.c-datepicker-time-spinner__input.c-datepicker-input .c-datepicker-input__inner,
.c-datepicker-time-spinner__list {
  padding: 0;
  text-align: center
}

.c-datepicker-time-spinner__list:after,
.c-datepicker-time-spinner__list:before {
  content: "";
  display: block;
  width: 100%;
  height: 80px
}

.c-datepicker-time-spinner__item {
  height: 32px;
  line-height: 32px;
  font-size: 12px;
  color: #333
}

.c-datepicker-scrollbar__bar.is-horizontal {
  height: 6px;
  left: 2px
}

.c-datepicker-scrollbar__bar.is-horizontal>div {
  height: 100%
}

.c-datepicker-scrollbar__thumb {
  position: relative;
  display: block;
  width: 0;
  height: 0;
  cursor: pointer;
  border-radius: inherit;
  background-color: rgba(144, 146, 152, .3);
  transition: background-color .3s
}

.c-datepicker-time-panel__footer {
  border-top: 1px solid #e4e4e4;
  padding: 4px 0;
  height: 36px;
  line-height: 25px;
  text-align: right;
  box-sizing: border-box;
  button{
    margin: 0 0 0 3px;
    &.min, &.max{
      border: 0;
      line-height: 28px;
      padding: 0 5px;
      cursor: pointer;
      background-color: transparent;
      outline: 0;
      font-size: 12px;
      color: #303133;
    }
    &:last-child{
      margin-right: 3px;
    }
  }
}

